





































































































import { Component, Prop, Vue } from 'vue-property-decorator';

interface Space {
  id: number;
  name: string;
  building: string;
  open: string;
  people: string;
  reservable: boolean;
  photo?: string;
  floor?: string;
  info?: string;
  tags?: string[];
  location?: string;
}

@Component
export default class StudySpace extends Vue {
  @Prop({
      type: Object as () => Space,
      default: () => {
          return {
            id: 0,
            name: 'Gurula',
            building: 'Exactum',
            floor: 'K',
            open: '8-19',
            people: '20',
            info: 'Gurula is the den of TKO-äly, the association of computer science students',
            tags: ['sofas', 'coffee', 'computers', 'help', 'food'],
          };
      },
  })
  public space!: Space;
}
